.container {
  height: 75px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 60px; }

.logo {
  position: fixed;
  top: 17px;
  left: 60px;
  font-size: 28px;
  font-weight: bold;
  background: #fff;
  padding: 4px 8px; }

.menu-container {
  position: fixed;
  top: 25px;
  right: 60px; }
  .menu-container .button {
    width: 30px;
    height: 30px;
    background: #fff;
    display: flex;
    justify-content: center; }
  .menu-container ul.menu {
    margin-top: 20px;
    padding: 12px 10px;
    background: #fff;
    display: none;
    position: absolute;
    right: 0; }
    .menu-container ul.menu.active {
      display: block; }
  .menu-container li {
    font-size: 20px; }
    .menu-container li:not(:last-child) {
      margin-bottom: 10px; }
